﻿.large-cart {
    &--btn-group__bottom {
        padding-top: 15px;
        padding-bottom: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__label {
        display: flex;
        justify-content: flex-start;
        align-items:center;
    }

    &--container {
        & .row {
            margin-right: 0px;
        }
    }

    &__margin-btn {
        margin-right: 20px;
    }

    &__margin-top{
        margin-top: 15px;
    }

    &--block {
        padding-top: 15px;
        padding-bottom: 15px;

        ul {
            list-style: none;
            padding: 0;
        }
    }
}
